
//

var Vue = require('vue')
var Vuex = require('vuex')

Vue.use(Vuex)


//
//
const state = {
    phone : '',//
    qrcode : '',//
    openid : '',
    collection: []  // 
}
// Vue.set(state,'shopCardatas',{});

//(shopCardatasgoodsidkey,)
const mutations = {//ajax data
    //this.$store.commit('setPhone', phone);
    ["setPhone"](state, phone){
        state.phone = phone;
    },
    ["clearPhone"](state){
        state.phone = '';    
    },
    ["setQrcode"](state, qrcode){
        state.qrcode = qrcode;
    },
    ["clearQrcode"](qrcode){
        state.qrcode = '';    
    },
    ["setOpenid"](state, openid){
        state.openid = openid;
    },
    ["clearOpenid"](state){
        state.openid = '';    
    },
}

/*
 gettersapp.vue,
 computed:{
     goodscount: function(){
     return this.$store.getters.getPhone;  //shopCarCount
     }
 }
* */
const getters = {
    getPhone : state =>{
        return state.phone;
    },
    getQrcode : state =>{
        return state.qrcode;
    },
    getOpenid : state =>{
        return state.openid;
    }
};

//actions ()
const actions = {
//  "setPhone":function({commit}, phone){
//      commit('setPhone', phone);//mutations
//  }
}


export default new Vuex.Store({
    state,
    mutations,
    actions,
    getters
})


